import Mock from 'mockjs'
import {Random} from 'mockjs'
// 
Mock.mock(/\/api\/login/, function (params) {
    const body = JSON.parse(params.body)
    return {
        "code": 0,
        "data": {
            username: body.username,
            token: Random.word(32),
            account: Random.word(12)
        }

    }
});
// 
Mock.mock(/\/api\/chat\/get\/userInfo/, function (params) {
    const body = JSON.parse(params.body);
    return {
        "code": 0,
        "data": {
            "name": Random.ctitle(2, 7),
            "account": body.account,
            "region": Random.county(true),
            "signature": Random.csentence(5, 30),
            "gender": Random.integer(1,2),
            "remark": Random.cname(),
            "avatar": Random.image(),
        }

    }
});
// 
Mock.mock(/\/api\/chat\/get\/recentList/, function () {
    const list = [];
    for (let i = 0; i < Random.natural(0, 10); i++) {
        let obj = {
            "id": Random.increment(1), // id
            "ctime": Random.date('yyyy/MM/dd'),//
            "content": Random.csentence(1, 50),//
            "unread": Random.integer(1,100),//1-100
            "avatar": Random.image(),
            "name": Random.ctitle(2, 7),//
            "account":Random.word(12),
            "target": Random.integer(1,2)
        }
        // 
        if (obj.target === 1) {
            obj = {
                ...obj,
                "region": Random.county(true),
                "signature": Random.csentence(5, 30),
                "gender": Random.integer(1,2),
                "remark": Random.cname(),
            }
        } else { // 
            obj = {
                ...obj,
                "note": Random.csentence(5, 30), // 
                "member": Random.integer(10, 200),
                "remark": Random.ctitle()
            }
        }
        list.push(obj)
    }
    return {
        "code": 0,
        "data": list

    }
});

// 
Mock.mock(/\/api\/chat\/get\/contactList/, function () {
    const friendList = [], groupList = [];
    for (let i = 0; i < Random.natural(0, 10); i++) {
        friendList.push({
            "id": Random.increment(1), // id
            "ctime": Random.date('yyyy/MM/dd'),//
            "name": Random.ctitle(2, 7),//
            "avatar": Random.image(), // 
            "remark": Random.cname(), // 
            "region": Random.county(true), //    
            "account": Random.word(12), // 12 
            "signature": Random.csentence(5, 30), // 5-30
            "gender": Random.integer(1,2), // 1|2
            "target": 1
        })
    }
    for (let i = 0; i < Random.natural(0, 10); i++) {
        groupList.push({
            "id": Random.increment(1), // id
            "ctime": Random.date('yyyy/MM/dd'),//
            "name": Random.ctitle(2, 7),//
            "avatar": Random.image(),
            "remark": Random.ctitle(), // 
            "target": 2,
            "note": Random.csentence(5, 30), // 
            "member": Random.integer(10, 200)
        })
    }
    return {
        "code": 0,
        "data": [{
            "title":"",
            "list": friendList,
            "total": friendList.length
        },
            {
                "title":"",
                "list": groupList,
                "total": groupList.length
            }]
    }
});
// 
Mock.mock(/\/api\/chat\/get\/memberList/, function (params) {
    const index = params.url.lastIndexOf('?')+1;
    const total = params.url.substr(index).split('=')[1];
    const list = []
    for (let i = 0; i < total; i++) {
        list.push({
                "id": Random.increment(1),
                "name": Random.ctitle(2, 7),
                "avatar": Random.image()
            })
    }

    return {
        "code": 0,
        "data": list
    }
})

// 
Mock.mock(/\/api\/chat\/get\/chatMsgList/, function (params) {
    const body = JSON.parse(params.body)
    const list = [];
    for (let i = 0; i < body.total; i++) {
        let obj = {
            "id": Random.increment(1),
            "chatContent":Random.csentence(1, 50),
            "targetType":Random.integer(1,2), // 1|2,
            "avatar": body.avatar,
            "ctime": Random.date('yyyy/MM/dd'),//
        }
        if (body.target === 1) {
            obj = {
                ...obj,
                "remark": body.remark,
                "region": body.region,
                "account": body.account,
                "gender": body.gender,
                "name": body.name,
                "source":""
            }
        } else {
            obj = {
                ...obj,
                "avatar": Random.image(), // 
                "remark": Random.cname(), // 
                "region": Random.county(true), //    
                "account": Random.word(12), // 12 
                "signature": Random.csentence(5, 30), // 5-30
                "gender": Random.integer(1,2), // 1|2
                "name": Random.ctitle(2, 7),
                "source":""
            }
        }
        list.push(obj)
    }

    return {
        "code": 0,
        "data": list
    }
})